.menu__primary.wp-block-pm-menu-fetcher {

	z-index: 2;
  	position: relative;

	& > ul.menu {
		position: relative;
		padding: 0;
		margin: 0;
		list-style: none;
		display: flex;
		flex-direction: row;
		justify-content: center;

		
		li.menu-item:hover, li.current-menu-item {
			a { color: var(--wp--preset--color--primary) !important }
		}
		& > li.menu-item {
		
			padding: var(--wp--preset--spacing--20) var(--wp--preset--spacing--40);
			a {
				letter-spacing: 0.125rem;
				text-transform: uppercase;
				color: var(--wp--preset--color--contrast-light);
				text-decoration: none;
				font-family: var(--wp--preset--font-family--highlight);
				
			}
			.sub-menu {
				display: none;
			}

			@media (min-width: 60rem) {

				&:hover {
	
					box-shadow: 0px 0px 0px 1000rem rgba(0,0,0,.1);
					& > a {
						background-color: var(--wp--preset--color--base);
						box-shadow:  0 0 0 1.25rem var(--wp--preset--color--base);
						border-radius: 1rem;
					}
	
					& > .sub-menu {
						display: block;
						position: absolute;
						top: 100%;
						left: 3rem;
						right: 3rem;
						background-color: var(--wp--preset--color--base);
						padding: var(--wp--preset--spacing--60) 0;
						border-radius: var(--wp--custom--border--radius--menu);
						li.menu-item {
							display: block;
							margin: 0;
							text-align: center;
							font-size: 2rem;
							a {
								display: inline-block;
								padding: var(--wp--preset--spacing--20) var(--wp--preset--spacing--90);
								text-transform: lowercase;
								font-family: var(--wp--preset--font-family--default)!important;
								color: var(--wp--preset--color--contrast) !important;
								min-width: 15rem;
							}
							&:hover {
								a { color: var(--wp--preset--color--primary) !important }
							}
						}
					}
				}

			}
		}
	}

} 

.wp-block-pm-menu-fetcher.menu__secondary {
	position: fixed;
	bottom: -0.75rem;
	margin: auto;
	width: 100%;
	font-family: var(--wp--preset--font-family--highlight);
	ul.menu {
		width: fit-content;
		display: flex;
		flex-direction: row;
		justify-content: center;
		gap: var(--wp--preset--spacing--50);
		list-style-type: none;
		margin:auto;
		padding: var(--wp--preset--spacing--40) var(--wp--preset--spacing--50) var(--wp--preset--spacing--50) var(--wp--preset--spacing--50);
		border-radius: 1.5rem;
		background-color:rgba(0,0,0,0.8);
		li.menu-item {
			a {
				text-decoration: none;
				color: white;
				text-transform: uppercase;
				font-size: .8rem;
				letter-spacing: .1rem;
				@media (max-width: 60rem) {
					font-size: .5rem;
				}
			}
		}
	}
}

footer {
	margin: 0;
}

.mentions-wide {
	max-width: none!important;
}

.wp-block-pm-menu-fetcher.menu__home-footer {

	@media (min-width: 60rem) {
		.menu {

			flex-direction: column!important;
		}
	}

	max-width:none;	
	ul.menu {
		padding: 0;
		margin: 0;
		display: flex;
		list-style: none;
		justify-content: space-between;
		li.menu-item {
			a {
				text-decoration: none;
			}
		}
	}
}

@media (max-width: 60rem) {

	#wpadminbar { display: none!important }

	.menu__primary:not(.--open) .menu {
		display: none!important;
	}

	.pm-menu__close-button:hover {
		cursor: pointer;
	}

	.pm-menu__close-button {
		font-size: 1rem;
		margin: 0;
		gap: .5rem;
		text-transform: uppercase;
		flex-direction: row;
		position: fixed;
		top: 2rem;
		left: 2rem;
		z-index: 1000;
		border: none;
		background: transparent;
		color: var(--wp--preset--color--contrast);
		font-family: var(--wp--preset--font-family--highlight);
	}

	.pm-menu__close-button::before {
		content: 'âœ•';
	}

	.menu__primary .pm-menu__close-button {
		display: none;
	}

	.menu__primary.--open .pm-menu__close-button {
		display: flex;
	}

	.menu__primary.--open .menu {
		position: fixed!important;
		top: 0;
		left: 0;
		right: 0;
		height: 100vh;
		background: var(--wp--preset--color--base-darker);
		z-index: 999;
		flex-direction: column!important;

		li a { color: black }
		li .sub-menu {
			display: flex!important;
			flex-direction: column;
			list-style-type: none;
		}
	}

}

@media (min-width: 60rem) {
	.pm-mobile-only {
		display: none!important;
	}
	
}

.pm-burger {
	position: relative;
	--slice-height: .2rem;
	--slice-space: .315rem;

	border: none;
	background: transparent;
	color: var(--wp--preset--color--primary);
	font-family: var(--wp--preset--font-family--highlight);

	padding: 2rem;
	display: flex;
	flex-direction: row;
	align-items: center;

	gap: .75rem;


}

.pm-burger:hover {
	cursor: pointer;
	color: var(--wp--preset--color--primary-dark)
}

