.pm-burger__label {
	width: 100%;
	color: inherit;
	text-transform: uppercase;
	font-family: var(--wp--preset--font-family--highlight);
}

.pm-burger__slices-container {
	aspect-ratio: 1;
	height: 1.25rem;
	display: inline-block;
	position: relative;
	vertical-align: baseline;
	z-index: 1;
}

.pm-burger__slice {
	height: var(--slice-height);
	background-color: currentColor;
	position: absolute;
	width: 100%;
	height: 3px;
	transform-origin: center;
	transform: rotate(0deg);
	transition: transform .25s, opacity .25s;
	border-radius: .2rem;
}

.pm-burger--menu-closed .pm-burger__slice:nth-child(1),
.pm-burger--menu-closed .pm-burger__slice:nth-child(4){
	opacity: 1;
}

.pm-burger--menu-open .pm-burger__slice:nth-child(1),
.pm-burger--menu-open .pm-burger__slice:nth-child(4){
	opacity: 0;
}

.pm-burger--menu-open .pm-burger__slice:nth-child(2){
	transform: rotate(45deg);
}
.pm-burger--menu-open .pm-burger__slice:nth-child(3){
	transform: rotate(-45deg);
}

.pm-burger__slice:nth-child(1){
	top: 0
}

.pm-burger__slice:nth-child(2){
	top: calc(var(--slice-height) + var(--slice-space))
}

.pm-burger__slice:nth-child(3){
	top: calc(var(--slice-height) + var(--slice-space))
}

.pm-burger__slice:nth-child(4){
	top: calc(2 * (var(--slice-space) + var(--slice-height)))
}